"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Menu, X, User, Search, Sun, Moon } from "lucide-react";
import { cn } from "@/lib/utils";
import { motion, AnimatePresence } from "framer-motion";
import { useTheme } from "next-themes";

const navLinks = [
    { name: "Home", href: "/" },
    { name: "Destinations", href: "/destinations" },
    { name: "Packages", href: "/packages" },
    { name: "Offers", href: "/offers" },
    { name: "Contact", href: "/contact" },
];

export function Navbar() {
    const [isScrolled, setIsScrolled] = useState(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const { theme, setTheme } = useTheme();
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        const handleScroll = () => {
            if (window.scrollY > 10) {
                setIsScrolled(true);
            } else {
                setIsScrolled(false);
            }
        };

        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    // Prevent hydration mismatch for theme
    if (!mounted) return null;

    return (
        <nav
            className={cn(
                "fixed top-0 left-0 right-0 z-50 transition-all duration-300 ease-in-out",
                isScrolled
                    ? "bg-white/80 dark:bg-gray-900/90 backdrop-blur-md shadow-sm py-4 text-gray-900 dark:text-white"
                    : "bg-transparent py-6 text-white"
            )}
        >
            <div className="container mx-auto px-4 md:px-6 flex items-center justify-between">
                {/* Logo */}
                <Link href="/" className="group">
                    <div className="bg-black px-4 py-2 rounded-xl shadow-lg border border-gray-800 group-hover:border-gray-700 transition-all duration-300">
                        <span className="text-2xl font-bold font-display tracking-wide text-red-600">TripMitra</span>
                        <span className="text-2xl font-bold font-display tracking-wide text-yellow-500">Aj</span>
                    </div>
                </Link>

                {/* Desktop Links */}
                <div className="hidden md:flex items-center space-x-8">
                    {navLinks.map((link) => (
                        <Link
                            key={link.name}
                            href={link.href}
                            className="text-sm font-medium hover:text-accent transition-colors"
                        >
                            {link.name}
                        </Link>
                    ))}
                </div>

                {/* Right Actions */}
                <div className="hidden md:flex items-center space-x-6">
                    <button
                        onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
                        className="hover:text-accent transition-colors p-2 rounded-full hover:bg-white/10"
                        aria-label="Toggle Theme"
                    >
                        {theme === "dark" ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
                    </button>
                    <button className="hover:text-accent transition-colors">
                        <Search className="w-5 h-5" />
                    </button>
                    <Link href="/login" className="flex items-center space-x-2 font-medium hover:text-accent transition-colors">
                        <User className="w-5 h-5" />
                        <span>Login</span>
                    </Link>
                    <Link
                        href="/book"
                        className={cn(
                            "px-5 py-2 rounded-full font-semibold transition-all hover:shadow-lg",
                            isScrolled
                                ? "bg-primary text-white hover:bg-primary/90"
                                : "bg-white text-primary hover:bg-gray-100"
                        )}
                    >
                        Book Now
                    </Link>
                </div>

                {/* Mobile Menu Button */}
                <button
                    className="md:hidden p-2"
                    onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                >
                    {isMobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
                </button>
            </div>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {isMobileMenuOpen && (
                    <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: "auto" }}
                        exit={{ opacity: 0, height: 0 }}
                        className="md:hidden bg-white dark:bg-gray-900 text-gray-900 dark:text-white overflow-hidden"
                    >
                        <div className="flex flex-col space-y-4 p-6">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.name}
                                    href={link.href}
                                    className="text-lg font-medium hover:text-primary"
                                    onClick={() => setIsMobileMenuOpen(false)}
                                >
                                    {link.name}
                                </Link>
                            ))}
                            <hr className="border-gray-200 dark:border-gray-800" />
                            <button
                                onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
                                className="flex items-center space-x-2 font-medium"
                            >
                                {theme === "dark" ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
                                <span>Toggle Theme</span>
                            </button>
                            <Link href="/login" className="flex items-center space-x-2 font-medium" onClick={() => setIsMobileMenuOpen(false)}>
                                <User className="w-5 h-5" />
                                <span>Login / Sign Up</span>
                            </Link>
                        </div>
                    </motion.div>
                )}
            </AnimatePresence>
        </nav>
    );
}
